body {
  background-color: white;
}

#root {
  padding: 0;
  margin: 0;
  width: 100%;
}

#root > div {
  margin-top: 20px;
  display: flex;
}

.map-container {
  height: 100vh;
  width: 40%;
  position: sticky !important; /* !important to override Gmaps inline CSS */
  top: 0 !important;
}

.place-list {
  padding-left: 10px;
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.place-list .card {
  width: calc(50% - 10px);
  margin-right: 10px;
  margin-bottom: 10px;
}

.marker {
  height: 20px;
  width: 20px;
  background-color: red;
  border-radius: 50%;
}


/* card */
.card {
  box-sizing: border-box;
  height: 230px;
  text-shadow: 0 1px 3px rgba(0,0,0,0.6);
  background-size: cover !important;
  background-position: center;
  color: white;
  position: relative;
}
.card.active {
  border: 10px solid red;
}
