.card-price {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 15px;
  text-transform: uppercase;
  background: grey;
  padding: 8px 12px;
  border-radius: 5px;
}
.card-description {
  position: absolute;
  bottom: 10px;
  left: 10px;
}
